<template>
    <div class="j-detail">
        <img v-for="(img, k) in imgs" :key="k" class="detailsImg" :src="img.url" alt="">
    </div>
</template>
<script>
import axios from "axios"
export default {
    name: "JDetail",
    data(){
        return {
            imgs:[]
        }
    },
    mounted(){
        this.init();
    },
    methods:{
        init(){
            const _this = this;
            let url = '../../../../../static/json/joanna/detail_' + this.$route.params.id + '.json'
            axios
                .get(url)
                .then(response => {
                    _this.imgs = response.data && response.data.data;
                })
                .catch(e=>{
                    console.error(e);
                })
        }
    }
}
</script>
<style>
.j-detail{
    margin: 0 10%;
}
.j-detail .detailsImg{
    display: inline-block;
    width: 100%;
    margin: 20px 0;
}
@media screen and (max-width: 560px) {
    .j-detail{
        margin: 0;
    }
    .j-detail .detailsImg{
        margin: 5px 0;
    }
}
</style>