<template>
    <div class="j-project">
        <main>
            <h1 class="title">Hello!</h1>
            <p class="text">I'm a UI | graphic designer, I love the Intenet art-related work.</p>
        </main>
        <div class="imgArea">
            <span class='imgContainer' v-for="(img, k) in imgs" :key="k">
                <img class="img" :src="img.url" @click="jumpToDetail(k)" />
            </span>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
    name:"JProject",
    data(){
        return{
            imgs:[]
        }
    },
    mounted(){
        const _this = this;
        axios
            .get('../../../../../static/json/joanna/project.json').then(response => {
                _this.imgs = response.data && response.data.data;
                console.log("joanna project imgs data:");
                console.log(_this.imgs);
            })
            .catch(e=>{
                console.error(e)
            })
    },
    methods:{
        jumpToDetail(index){
            this.$router.push({ path: "/joanna/detail/" + (index+1)})
        }
    }
}
</script>
<style>
    .j-project main{
        padding: 50px 10%;
        background: #e4ffdd;
        text-align: left;
    }
    .j-project main .text{
        color: #808080;
        padding-top: 10px;
        font-size: 14px;
    }
    .j-project .imgArea{
        background: #fff;
        padding: 0 10%;
        font-size: 0;
    }
    .j-project .imgArea .imgContainer{
        display: inline-block;
        width: 50%;
        overflow: hidden;
    }
    .j-project .imgContainer .img{
        display: inline-block;
        width: 100%;
        height: 100%;
        background-size: 100% 100%;
        transition: all 1s;
        cursor:pointer;
    }
    .j-project .imgContainer .img:hover{
        transform: scale(1.2);
    }
    @media screen and (max-width: 560px){
        .j-project main{
            padding: 10px;
        }
        .j-project main .text{
            font-size: 12px;
        }
        .j-project .imgArea{
            padding: 0;
        }
    }
</style>