<template>
    <div class="joanna">
        <div class="joanna-header" :style="{background: $route.name === ('j-about' || 'j-detail') ? '' : '#e4ffdd'}">
            <p class="name">JOANNA</p>
            <p class="guide">
                <a v-for="item in items" :key="item.index" :class="'j-' + item.name.toLowerCase() ===  $route.name ? 'current' : ''" @click="jump(item.href)">
                    {{item.name}}
                </a>
            </p>
        </div>
        <router-view></router-view>
    </div>
</template>
<script>
export default {
    name:"JHeader",
    data(){
        return {
            items:[
                {index: 0, name: "Project", href: "/joanna/project"},
                {index: 1, name: "About", href: "/joanna/about"},
                {index: 2, name: "Contact", href: "/joanna/contact"}
            ]
        }
    },
    methods:{
        jump(path){
            this.$router.push({path: path});
        }
    }
}
</script>
<style>
.joanna-header{
    position: relative;
    margin: 0 auto;
    background: #fff;
    height: 60px;
    line-height: 60px;
    font-size: 18px;
    font-family: "Century Gothic", CenturyGothic, AppleGothic, sans-serif;
}
.joanna-header .name{
    position: absolute;
    top: 0;
    left: 10%;
    font-weight: bold;
}
.joanna-header .guide{
    position: absolute;
    top: 0;
    right: 10%;
}
.joanna-header .guide .current{
    color: #000;
}
.joanna-header .guide>a{
    display: inline-block;
    margin: 0 10px;
    text-decoration: none;
    color: #808080;
}
@media screen and (max-width: 560px){
    .joanna-header{
        background: #fff;
        height: 40px;
        line-height: 40px;
        font-size: 14px;
    }
    .joanna-header .name{
        left: 10px;
    }
    .joanna-header .guide{
        right: 10px;
    }
    .joanna-header .guide>a{
        margin: 0 5px;
    }
}
</style>