<template>
    <div class="index-page">
        <!-- 轮播图 -->
        <Carousel :modal="0" loop autoplay 
        :autoplay-speed="setting.autoplaySpeed" 
        :dots="setting.dots" 
        :radius-dot="setting.radiusDot"
        :trigger="setting.trigger"
        :arrow="setting.arrow">
            <CarouselItem v-for="(item, key) in [1,2,3,4,5]" :key="key">
                <img class="index-backImg" :src="require('assets/img/backIndex_' + item + '.png')"/> 
            </CarouselItem>
        </Carousel>
        <div class="img-intro">
            <p class="img-title">{{$t('page.index.welcome')}}</p>
            <p class="img-text">{{$t('page.index.welcomeText')}}</p>
        </div>
        <div class="cardArea">
            <Tabs type="card">
                <TabPane v-for="tab in tabs" :key="tab.index" :icon="tab.icon" :closable="tab.closable" :disabled="tab.disabled" :label="tab.label">
                    <v-index-page-module v-for="(card, k) in tab.cards" :key="k" :card="card" :tabIndex="tab.index" :k="k"></v-index-page-module>
                </TabPane>
                <Icon class="tabBtnAdd" type="ios-add-circle-outline" slot="extra" size="28" color="#5cadff" @click="handleTabsAdd"/>
            </Tabs>
        </div>
    </div>
</template>
<script>
import VIndexPageModule from "./IndexPageModule";

export default {
    name: "VPage", 
    components: { VIndexPageModule },
    data(){
        return {
            setting: {
                autoplay: true,
                autoplaySpeed: 5000,
                dots: "inside",
                radiusDot: false,
                trigger: "click",
                arrow: "hover"
            },
            tabs: [],
        }
    },
    mounted(){
        //TODO: fake data
        this.tabs = [
            {
                index: 0, 
                icon: "ios-backspace-outline", 
                closable: false, 
                disabled: false, 
                label: "我的空间",
                cards: [
                    {
                        name: "1_working_space", 
                        urlApi: "../../../../static/json/spaceDataWorking.json"
                    },
                    { 
                        name: "2_learning_space", 
                        urlApi: "../../../../static/json/spaceDataLearning.json"
                    },
                    {
                        name: "3_living_space", 
                        urlApi: "../../../../static/json/spaceDataLiving.json"
                    },
                    { 
                        name: "4_gaming_space", 
                        urlApi: "../../../../static/json/spaceDataGaming.json"
                    },
                    { 
                        name: "5_management_space", 
                        urlApi: "../../../../static/json/spaceDataManagement.json"
                    }
                ]
            },
            {
                index: 1, 
                icon: "ios-card-outline", 
                closable: true, 
                disabled: false, 
                label: "主题卡片", 
                cards: [
                    {
                        name:"1_wordOfTank_card", 
                        urlApi:"../../../../static/json/themeDataPoem.json"
                    },
                    {
                        name:"2_rasyoumonn_card", 
                        urlApi:"../../../../static/json/themeDataWOT.json"
                    },
                    {
                        name:"1_wordOfTank_card", 
                        urlApi:"../../../../static/json/themeDataSong.json"
                    },
                    {
                        name:"1_wordOfTank_card", 
                        urlApi:"../../../../static/json/themeDataMovie.json"
                    },
                    {
                        name:"1_wordOfTank_card", 
                        urlApi:"../../../../static/json/themeDataTravel.json"
                    }
                ]
            }
        ];
    },
    methods: {
        handleTabsAdd(){
            let defaultAddTabObj = {
                index: this.tabs.length,
                icon: "ios-add-circle-outline",
                closable: true,
                disabled: false,
                label: "lable",
                moduleIds: []
            }
            this.tabs.push(defaultAddTabObj);
        }
    }
}
</script>
<style>
    .index-page{
        position: relative;
        min-height: calc(100vh - 60px);
        background: aliceblue;
    }
    .index-page .index-backImg{
        display: block;
        width: 100vw;
        height: calc(100vh - 60px);
        background-size: cover; 
    }
    .index-page .img-intro{
        position: absolute;
        top: 30vh;
        left: 0;
        right: 0;
        min-width: 500px;
        max-width: 1000px;
        margin: 0 auto;
        padding: 20px 50px;
        color: #fff;
    }
    .index-page .img-title{
        font-size: 36px;
        height: 60px;
        line-height: 50px;
    }
    .index-page .img-text{
        font-size: 22px;
    }

    .cardArea{
        margin-top: 50px;
    }
    .cardArea .tabBtnAdd{
        cursor: pointer;
    }
</style>