import Vue from 'vue'
import Router from 'vue-router'
// import Index from '@/components/Index'
import Header from '@/components/common/VHeader'
import Page from '@/components/page/Page'
import PageIndex from '@/components/page/Index'
import PageIndexWorking from '@/components/page/module/Working'
import PageIndexLearning from '@/components/page/module/Learning'
import PageIndexLiving from '@/components/page/module/Living'
import PageIndexGaming from '@/components/page/module/Gaming'
import PageIndexMgmt from '@/components/page/module/Management'
import JHeader from '@/components/joanna/common/Header'
import JProject from '@/components/joanna/pages/Project'
import JAbout from '@/components/joanna/pages/About'
import JContact from '@/components/joanna/pages/Contact'
import JDetail from '@/components/joanna/pages/Detail'
import VueCookies from 'vue-cookies'

Vue.use(Router)
Vue.use(VueCookies)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/kalkin/index'
    },
    {
      path: '/kalkin',
      component: Header,
      children: [
        {
          path: '',
          redirect: 'index'
        },
        {
          path: 'index',
          name: 'index',
          component: PageIndex,
          meta: {
            title: "route.title.index"
          }
        },
        {
          path: 'working',
          name: 'working',
          component: PageIndexWorking,
          meta: {
            title: "route.title.working"
          }
        },
        {
          path: 'learning',
          name: 'learning',
          component: PageIndexLearning,
          meta: {
            title: "route.title.learning"
          }
        },
        {
          path: 'living',
          name: 'living',
          component: PageIndexLiving,
          meta: {
            title: "route.title.living"
          }
        },
        {
          path: 'gaming',
          name: 'gaming',
          component: PageIndexGaming,
          meta: {
            title: "route.title.gaming"
          }
        },
        {
          path: 'management',
          name: 'management',
          component: PageIndexMgmt,
          meta: {
            title: "route.title.management"
          }
        },
        {
          path: ':pageId',
          name: 'page',
          component: Page,
          meta: {
            title: "route.title.page"
          },
        }
      ]
    },
    {
      path: '/joanna',
      component: JHeader,
      children:[
        {
          path: "",
          redirect: '/joanna/project',
        },
        {
          path:"project",
          name:"j-project",
          component: JProject,
          meta: {
            title: "route.title.page"
          }
        },
        {
          path:"about",
          name:"j-about",
          component: JAbout,
          meta: {
            title: "route.title.page"
          }
        },
        {
          path:"contact",
          name:"j-contact",
          component: JContact,
          meta: {
            title: "route.title.page"
          }
        },
        {
          path: "detail/:id",
          name: "j-detail",
          component: JDetail,
          meta: {
            title: "route.title.page"
          }
        }
      ]
    }
  ]
})
