<template>
    <div class="index-page-module">
        <div class="space-wrapper" @click="clickCard">
            <Card v-show="tabIndex === 0" :style="setBackColor()">
                <p class="card-title" slot="title" :title="cardData.title">{{cardData.title}}</p>
                <Row class="space-module code-row-bg" type="flex" justify="center">
                    <Col span="4">
                        <div class="space-desc">
                            <div v-for="de in cardDescSep" :key="de">{{"     "+de}}</div>
                        </div>
                    </Col>
                    <Col span="16">
                        <div class="img-split">
                            <img v-for="(img, i) in cardData.imgMsg" :key="i" :src="img.path" :alt="$t('error.imgErrorMsg')">
                        </div>
                    </Col>
                    <Col span="4">
                        <div class="other-msg">
                            {{cardData.otherMsg}}
                        </div>
                    </Col>
                </Row>
            </Card>
        </div>
        <div class="card-theme-wrapper" @click="clickCard">
            <Col v-show="tabIndex === 1" class="card-theme" span="4">
                <Card>
                    <p class="card-title" slot="title">
                        <span class="theme">{{cardData.theme}}</span>
                        {{cardData.title}}
                    </p>
                    <div class="cardThemeContent">
                        <div class="text">{{cardData.content}}</div>
                    </div>
                </Card>
            </Col>
        </div>
    </div>
</template>
<script>
import axios from "axios"
export default {
    name: "VIndexPageModule",
    data(){
        return{
            cardData: {},
            poemData: {},
            cardDescSep:[]
        }
    },
    props:{
        "card": {
            type: Object,
            required: true
        },
        "tabIndex": {
            type: Number,
            required: true
        },
        "k": {
            type: Number,
            required: false
        }
    },
    mounted(){
        this.init();
    },
    methods: {
        init(){
            let _this = this;
            axios.get(this.card.urlApi).then(response => {
                if(response.status === 200){
                    _this.cardData = response.data;
                    console.log(_this.cardData);
                    _this.cardDescSep = _this.cardData.desc && _this.cardData.desc.split(" ");
                }else{
                    console.error("response status: " + response.status)
                }
            }, err => {
                console.error(err);
            })
        },
        clickCard(){
            this.$router.push({ path: this.cardData.route });
        },
        setBackColor(){
            return { background: this.k % 2 ? "linear-gradient(to right, white, lightblue)" : "linear-gradient(to right, lightblue, white)" };
        }
    }
}
</script>
<style>
    .index-page-module{
        margin: 10px 20px;
        cursor: pointer;
    }
    .index-page-module .space-module{
        height: 30vh;
    }
    .index-page-module .space-module .ivu-col-span-16{
        border: 1px solid #eee;
        border-top: 0;
        border-bottom: 0;
    }
    .index-page-module .space-module .ivu-col{
        padding: 10px 20px;
    }
    .index-page-module .space-module .space-desc{
        white-space: pre-wrap;
        text-align: left;
    }
    .index-page-module .card-theme{
        position: relative;
        padding: 10px;
    }
    .index-page-module .card-theme .ivu-card{
        height: 30vh;
    }
    .index-page-module .card-title{
        color: #3cc;
    }
    .index-page-module .theme{
        display: inline-block;
        position: absolute;
        left: 0;
        top: 0;
        padding: 15px;
        background: antiquewhite;
        border-radius: 50%;
        transition: all .5s linear;
        transform: translateX(300px);
        transform: rotate(360deg);
    }
    .index-page-module .card-theme:hover .theme{
        background: #3cc;
        color: white;
    }
    .index-page-module .card-theme:hover .theme:nth-child(even){
        transform: rotate(0);
    }
    .index-page-module .card-theme:hover .theme:nth-child(odd){
        transform: translateX(0);
    }
    .index-page-module .space-module .img-split{
        height: 100%;
    }
    .index-page-module .space-module .img-split>img{
        display: block;
        float: left;
        width: 20%;
        height: 100%;
        border-radius: 50%;
        box-sizing: border-box;
        padding: 10px;
        object-fit: cover;
        background: azure;
        transition: transform .2s linear;
    }
    .index-page-module .space-module .img-split>img:hover{
        transform: scale(1.1);
    }
    .index-page-module .cardThemeContent .title{
        padding-bottom: 20px;
        font-size: 20px;
        font-weight: bold;
    }
    .index-page-module .cardThemeContent .text{
        white-space: pre-wrap;
    }
</style>